



































import Vue from 'vue'
import { mapState } from 'vuex'
import ModalFooterIcons from './FooterIcons.vue'

export default Vue.extend({
  components: { ModalFooterIcons },
  computed: mapState(['merge', 'sheet']),

  methods: {
    setLocation(ui: string) {
      this.$store.dispatch('setLocation', ui)
    }
  },
  mounted(): void {
    window._backend.sheetUIIncreaseMergesCounter()

    /* eslint-disable @typescript-eslint/camelcase */

    window.gtag('event', 'sent_campaign', {
      event_category: 'engagement',
      event_label: 'scheduled',
      value: this.sheet.emailing.recipients
    })

    window.saasmetrics('event', {
      category: 'engagement',
      action: 'emails_sent',
      label: 'scheduled',
      value: this.sheet.emailing.recipients
    })
  }
})
