import Vue from 'vue'
import { ActionTree, MutationTree } from 'vuex'
import * as firebase from 'firebase/app'
import 'firebase/database'
import Types from '../../types'

// State
const state: Types.Campaign[] = []

const actions: ActionTree<Types.Campaign[], Types.RootState> = {
  /**
   * Initializer
   */

  initialize(context, campaigns: Types.CampaignMinimal[]) {
    context.dispatch('fetchAll', campaigns)
  },

  /**
   * Fetch all campaigns details from the database
   * and save them locally (in the store)
   */

  fetchAll(context, campaigns: Types.CampaignMinimal[]) {
    const db = firebase.database()

    for (let i = 0, n = campaigns.length; i < n; i++) {
      const campaign = campaigns[i]
      const cid = campaign.cid
      const column = {
        id: campaign.column,
        name: campaign.columnName,
        heading: campaign.columnHeading
      }

      const ref = db.ref('campaigns/' + cid)

      ref.on('value', snapshot => {
        const campaignData = snapshot.val()

        if (!campaignData || !campaignData.metadata) {
          return
        }

        const campaign = {
          cid: cid,
          name: campaignData.metadata.name,
          column: column.id,
          columnName: column.name,
          columnHeading: column.heading,
          createdAt: campaignData.metadata.createdAt,
          doNotTrack: campaignData.metadata.doNotTrack,
          metrics: {
            emails: campaignData.metrics.e || 0,
            opens: campaignData.metrics.o || 0,
            clicks: campaignData.metrics.c || 0
          }
        }

        context.commit('localUpdate', campaign)
        context.dispatch('events/listener', campaign, { root: true })
      })
    }
  }
}

const mutations: MutationTree<Types.Campaign[]> = {
  /**
   * Update a template locally (to the store)
   */

  localUpdate(state, campaign: Types.Campaign) {
    const campaigns = state

    // Try to update the campaign (if already stored)
    for (let i = 0, n = campaigns.length; i < n; i++) {
      if (campaigns[i].cid === campaign.cid) {
        return Vue.set(state, i, campaign)
      }
    }

    // Otherwise push it
    campaigns.push(campaign)

    state = campaigns.sort((a, b) => {
      return b.column > a.column ? -1 : 1
    })
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations
}
