
































import Vue from 'vue'

type Day = {
  n: number
  date: Date
  active?: boolean
  disabled?: boolean
  selected?: boolean
  today?: boolean
}

export default Vue.extend({
  props: ['selection'],
  data() {
    return {
      day: new Date(),
      weekdays: ['S', 'M', 'T', 'W', 'T', 'F', 'S'],
      months: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December']
    }
  },
  computed: {
    selectedDay(): Date | null {
      const selection = new Date(this.selection)

      if (!selection || !(selection instanceof Date)) {
        return null
      }

      selection.setHours(0)
      selection.setMinutes(0)
      selection.setSeconds(0)
      selection.setMilliseconds(0)

      return selection
    },
    days(): Day[] {
      const today = new Date()
      today.setHours(0)
      today.setMinutes(0)
      today.setSeconds(0)
      today.setMilliseconds(0)

      const todayTime = today.getTime()
      const selectionTime = this.selectedDay ? this.selectedDay.getTime() : -1

      const activeDay = new Date(this.day)
      activeDay.setHours(0)
      activeDay.setMinutes(0)
      activeDay.setSeconds(0)
      activeDay.setMilliseconds(0)

      const activeMonth = new Date(activeDay)
      activeMonth.setDate(1)
      const prevMonth = new Date(activeMonth)
      prevMonth.setMonth(activeMonth.getMonth() - 1)
      const nextMonth = new Date(activeMonth)
      nextMonth.setMonth(activeMonth.getMonth() + 1)

      // Date helper
      const isActive = nextMonth.getTime() > todayTime

      // Days array (aka results)
      let days: Day[] = []

      const whichDay = function(month: Date, i: number, inactive?: boolean): Day {
        const day = new Date(month)
        day.setDate(i + 1)

        const dayTime = day.getTime()

        const disabled = todayTime > dayTime
        const active = disabled || typeof inactive === 'boolean' ? inactive : isActive
        const today = !disabled && dayTime === todayTime
        const selected = !disabled && dayTime === selectionTime

        return { n: i + 1, date: day, disabled, active, today, selected }
      }

      // Start with previous month
      const daysInPrevMonth = this.daysInMonth(prevMonth)
      for (let i = 0; i < daysInPrevMonth; i++) {
        days.push(whichDay(prevMonth, i, false))
      }

      // Adds up current month
      const daysInCurrMonth = this.daysInMonth(activeMonth)
      for (let i = 0; i < daysInCurrMonth; i++) {
        days.push(whichDay(activeMonth, i))
      }

      // Adds up next month
      const daysInNextMonth = this.daysInMonth(nextMonth)
      for (let i = 0; i < daysInNextMonth; i++) {
        days.push(whichDay(nextMonth, i, false))
      }

      // Focus on current month
      days = days.slice(daysInPrevMonth - activeMonth.getDay())
      days = days.slice(0, 7 * 6)

      return days
    },

    month(): string {
      const activeDay = this.day
      const month = this.months[activeDay.getMonth()]
      const year = activeDay.getUTCFullYear()
      return `${month} ${year}`
    }
  },
  methods: {
    daysInMonth(date: Date): number {
      const d = new Date(date.getFullYear(), date.getMonth() + 1, 0)
      return d.getDate()
    },

    setActiveMonth(i: number): void {
      const activeDay = this.day
      activeDay.setMonth(activeDay.getMonth() + i)
      this.day = new Date(activeDay.getFullYear(), activeDay.getMonth())
    },

    setSelection(day: Day): void {
      if (day.disabled) {
        return
      }

      this.$emit('selected', day.date)
    }
  }
})
