





























































































































import Vue from 'vue'
import Calendar from './Calendar.vue'
import ModalFooterIcons from './FooterIcons.vue'

export default Vue.extend({
  data() {
    return {
      selectionDate: new Date(),
      startDateInput: '',
      startTimeInput: '',
      startDateValidInput: true,
      startTimeValidInput: true
    }
  },
  computed: {
    startDate: {
      get(): string {
        if (!this.startDateInput) {
          return this.formatDay(this.selectionDate)
        }

        return this.startDateInput
      },
      set(newDate: string): void {
        this.startDateInput = newDate
        const time = Date.parse(newDate)

        if (isNaN(time)) {
          this.startDateValidInput = false // Display error to user (like Gmail)
          return
        }

        const date = new Date(time)
        date.setHours(this.selectionDate.getHours())
        date.setMinutes(this.selectionDate.getMinutes())

        const now = new Date().getTime() - 60 * 60 * 1000

        if (date.getTime() < now) {
          this.startDateValidInput = false
          return
        }

        this.startDateValidInput = true
        this.setSelectedDate(date)

        return
      }
    },
    startTime: {
      get(): string {
        if (!this.startTimeInput) {
          return this.formatTime(this.selectionDate)
        }

        return this.startTimeInput
      },
      set(newTime: string): void {
        this.startTimeInput = newTime

        const regex = new RegExp('^ *([0-9]{1,2}):([0-9]{2}) *(AM|PM) *$')
        const matches = newTime.match(regex)

        if (!matches || matches.length !== 4) {
          this.startTimeValidInput = false
          return
        }

        let hours = Number(matches[1])
        const minutes = Number(matches[2])
        const ampm = matches[3]

        if (isNaN(hours) || isNaN(minutes) || hours > 24 || minutes > 60) {
          this.startTimeValidInput = false
          return
        } else if (hours >= 12 && ampm.toUpperCase() === 'PM') {
          this.startTimeValidInput = false
          return
        }

        if (hours < 12) {
          hours += ampm === 'PM' ? 12 : 0
        }

        const newDate = new Date(this.selectionDate)
        newDate.setHours(hours)
        newDate.setMinutes(minutes)

        const now = new Date().getTime() - 60 * 1000

        if (newDate.getTime() < now) {
          this.startTimeValidInput = false
          return
        }

        this.startTimeValidInput = true
        this.selectionDate = newDate
      }
    }
  },
  components: {
    Calendar,
    ModalFooterIcons
  },
  methods: {
    setLocation(ui: string) {
      this.$store.dispatch('setLocation', ui)
    },
    formatDate(dateObj: Date): string {
      if (typeof dateObj.toLocaleString !== 'function') {
        return dateObj.toString()
      }

      const date = dateObj.toLocaleString('en-US', { year: 'numeric', month: 'long', day: 'numeric' })
      const time = dateObj.toLocaleString('en-US', { hour: '2-digit', minute: '2-digit' })

      return `${date} at ${time}`
    },
    formatDay(date: Date): string {
      const months = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December']

      const month = months[date.getMonth()]
      const day = date.getDate()
      const year = date.getFullYear()
      return `${month} ${day}, ${year}`
    },
    formatTime(date: Date): string {
      let hours = date.getHours()
      const minutes = date.getMinutes()
      const ampm = hours >= 12 ? 'pm' : 'am'
      hours = hours % 12
      hours = hours ? hours : 12 // the hour '0' should be '12'
      return `${hours}:${('0' + minutes).slice(-2)} ${ampm}`.toUpperCase()
    },
    setSelectedDate(date: Date): void {
      const newSelection = new Date(date)
      newSelection.setHours(this.selectionDate.getHours())
      newSelection.setMinutes(this.selectionDate.getMinutes())

      this.startDateValidInput = true
      this.selectionDate = newSelection
      this.startDate = this.formatDay(newSelection)
    }
  }
})
