



























import Vue from 'vue'

export default Vue.extend({
  computed: {
    active(): boolean {
      return this.$store.state.butterbar.active
    },
    theme(): string {
      return this.$store.state.butterbar.message
    },
    message(): string {
      let errorMessage = this.$store.state.butterbar.message as string

      if (errorMessage.substr(0, 7) === 'Error: ') {
        errorMessage = errorMessage.substr(7)
      } else if (errorMessage.substr(0, 6) === 'ERROR_') {
        errorMessage = String(this.$i18n.t(errorMessage))
      }

      return errorMessage || String(this.$i18n.t('ERROR_UNHANDLED'))
    },
    link(): string | boolean {
      let link = this.$store.state.butterbar.link as string

      if (!link) {
        link = 'https://support.mailmeteor.com/help/troubleshooting'
      } else if (link.substr(0, 6) === 'ERROR_') {
        link = String(this.$i18n.t(link))
      }

      return link
    }
  },
  methods: {
    close: function() {
      this.$store.dispatch('closeButterbar')
    }
  }
})
