import Vue from 'vue'
import firebase from 'firebase/app'
import App from './App.vue'
import store from './store'
import i18n from './i18n'
import Types from './types'

new Vue({
  store,
  i18n,
  created(): void {
    /**
     * Initialize firebase
     */

    firebase.initializeApp(window._app && window._app.firebase)

    /**
     * Fetch user data from Google Apps Script
     */

    const $store = this.$store
    const mode = window._app && window._app.mode

    if (mode === 'sidebar') {
      window._backend
        // TODO - Handle "withFailureHandler" to send sidebar-errors to bugsnag
        .withSuccessHandler((data: { user: Types.UserProfile; campaigns: Types.Campaign[] }) => {
          // Initialize store's modules
          $store.dispatch('user/initialize', data.user)
          $store.dispatch('campaigns/initialize', data.campaigns)
          $store.dispatch('events/timer')

          // Handle app's location
          $store.dispatch('setLocation', (window._app && window._app.location) || '/sidebar')

          // Set the app is ready (initialized)
          window.setTimeout(function() {
            $store.dispatch('ready', true)
          }, 300)
        })
        .withFailureHandler(this.$root.errorBackendHandler)
        .sheetUIDataSidebar()
    } else {
      window._backend
        .withSuccessHandler((data: { user: Types.UserProfile; sheet: Types.Sheet }) => {
          const user = data.user
          const sheet = data.sheet

          // Initialize store's modules
          $store.dispatch('user/initialize', user)
          $store.dispatch('sheet/initialize', sheet)
          $store.dispatch('aliases/initialize', (user && user.aliases) || [])
          $store.dispatch('merge/initialize', user)

          // Handle app's location ("/" by default)
          if (!user.installedAt) {
            $store.dispatch('setLocation', '/onboarding')
          } else {
            $store.dispatch('setLocation', (window._app && window._app.location) || '/')
          }

          if (!user.premium && user.mergesCounter > 0) {
            const mergesCounter = user.mergesCounter
            if (mergesCounter === 2 || mergesCounter === 4) {
              // Propose to add a review at 2 and 4 merges
              // (if user hasn't been doubled yet)
              if (user.quota.limit < 200) {
                $store.dispatch('showSplashScreen', 'add-review')
              } else {
                $store.dispatch('showSplashScreen', 'discount')
              }
            } else if (mergesCounter > 4 && mergesCounter % 3 === 0) {
              // ... or every 3 merges starting at 6 (so at 6, 9, 12, etc.)
              $store.dispatch('showSplashScreen', 'discount')
            }
          }

          // Set the app is ready (initialized)
          window.setTimeout(function() {
            $store.dispatch('ready', true)
          }, 200)

          /* eslint-disable-next-line @typescript-eslint/camelcase */
          window.gtag('event', 'user_event', { user_account: user.premium ? 'Paid' : 'Free' })
        })
        .withFailureHandler(this.$root.errorBackendHandler)
        .sheetUIDataModal()
    }
  },
  methods: {
    errorBackendHandler(error: Error): void {
      let errorMessage = (error && error.message) || ''
      let errorLink = ''

      if (errorMessage.substr(0, 13) === 'ScriptError: ') {
        errorMessage = errorMessage.substr(13)
      } else if (errorMessage.substr(0, 7) === 'Error: ') {
        errorMessage = errorMessage.substr(7)
      } else if (errorMessage.substr(0, 11) === 'Exception: ') {
        errorMessage = errorMessage.substr(11)
      }

      // Try to catch authentication error
      // (double account at 90%)
      if (
        errorMessage.indexOf('Authorization is required to perform that action') > -1 ||
        errorMessage.indexOf('Authorisation is required to perform that action') > -1 ||
        errorMessage.indexOf('এই ক্রিয়াটি সম্পাদনা করার জন্য অনুমোদন প্রয়োজন') > -1 ||
        errorMessage.indexOf('Для виконання цієї дії потрібно здійснити авторизацію') > -1 ||
        errorMessage.indexOf('Perlu otorisasi untuk melakukan tindakan itu') > -1 ||
        errorMessage.indexOf('Se necesita autorización para realizar esta acción.') > -1 ||
        errorMessage.indexOf('Se requiere autorización para realizar esa acción.') > -1 ||
        errorMessage.indexOf('Cal tenir autorització per efectuar aquesta acció.') > -1 ||
        errorMessage.indexOf('Este necesară autorizarea pentru a efectua acțiunea respectivă.') > -1 ||
        errorMessage.indexOf('autorização para efetuar') > -1 ||
        errorMessage.indexOf('autorização para executar') > -1 ||
        errorMessage.indexOf('Kailangan ng awtorisasyon upang maisagawa ang aksyon na iyan.') > -1 ||
        errorMessage.indexOf('A művelet végrehajtásához engedély szükséges.') > -1 ||
        errorMessage.indexOf('Da biste izvršili tu akciju, potrebna je autorizacija.') > -1 ||
        errorMessage.indexOf('वह कार्यवाही करने के लिए अधिकार की आवश्यकता है.') > -1 ||
        errorMessage.indexOf('ती क्रिया करण्यासाठी अधिकृतता आवश्यक आहे.') > -1 ||
        errorMessage.indexOf('এই ক্রিয়াটি সম্পাদনা করার জন্য অনুমোদন প্রয়োজন৷') > -1 ||
        errorMessage.indexOf('Godkännande krävs för att utföra denna åtgärd.') > -1 ||
        errorMessage.indexOf('Do wykonania tej czynności wymagana jest autoryzacja.') > -1 ||
        errorMessage.indexOf('Vous devez disposer des autorisations requises pour pouvoir effectuer cette action.') > -1 ||
        errorMessage.indexOf('Autorisation requise pour exécuter cette action. Exécutez à nouveau le script pour autoriser cette action.') > -1 ||
        errorMessage.indexOf('Für die Ausführung dieser Aktion ist eine Berechtigung erforderlich.') > -1 ||
        errorMessage.indexOf('K provedení dané akce je vyžadována autorizace.') > -1 ||
        errorMessage.indexOf('Perlu otorisasi untuk melakukan tindakan itu.') > -1 ||
        errorMessage.indexOf('За да извършите това действие, ви е необходимо разрешение.') > -1 ||
        errorMessage.indexOf('その操作を実行するには承認が必要です。') > -1 ||
        errorMessage.indexOf('அந்தச் செயலைச் செய்ய அங்கீகரிப்பு தேவைப்படுகிறது.') > -1 ||
        errorMessage.indexOf('Для виконання цієї дії потрібно здійснити авторизацію.') > -1 ||
        errorMessage.indexOf('Для выполнения этого действия необходима авторизация.') > -1 ||
        errorMessage.indexOf('Autorisation er påkrævet') > -1 ||
        errorMessage.indexOf("richiesta l'autorizzazione") > -1 ||
        errorMessage.indexOf('toestemming nodig') > -1 ||
        errorMessage.indexOf('Bu eylemi gerçekleştirmek için yetki gerekiyor') > -1 ||
        errorMessage.indexOf('需要授權才能執行此動作。') > -1 ||
        errorMessage.indexOf('Toiminnon tekemiseen vaaditaan lupa.') > -1 ||
        errorMessage.indexOf('Cần được cho phép để thực hiện') > -1
      ) {
        errorMessage = 'ERROR_LACKING_AUTHORIZATION'
        errorLink = 'ERROR_LACKING_AUTHORIZATION_LINK'
      }

      // Issue with Firebase
      else if (errorMessage.indexOf('.cloudfunctions.net') > 1) {
        errorMessage = 'ERROR_FIREBASE_FAILED'
        errorLink = 'ERROR_FIREBASE_FAILED_LINK'
      }

      // Show butter bar
      this.$store.dispatch('showButterbar', { message: errorLink ? errorMessage : '', link: errorLink })

      // Log error (with report to bugsnag)
      window._logger(error)
    }
  },

  render: h => h(App)
}).$mount('#app')
