import Vue from 'vue'
import { ActionTree, MutationTree } from 'vuex'
import * as firebase from 'firebase/app'
import 'firebase/auth'
import Types from '../../types'

const state = {
  isLogged: false,
  uid: '',
  name: '',
  email: '',
  domain: '',
  subscription: '',
  createdAt: '',
  installedAt: '',
  mergesCounter: 0,
  quota: { limit: 100, available: 100, usage: 0, refreshDate: '' },
  notificationsUnread: false,
  blocked: false,
  premium: false,
  aliases: []
}

const actions: ActionTree<Types.UserProfile, Types.RootState> = {
  /**
   * Initializer
   */

  initialize(context, user) {
    if (user) {
      context.commit('set', user)
    }

    context.dispatch('signin', { at: new Date(), token: user && user.token, uid: user.uid })
  },

  /**
   * Handle login
   */

  signin(context, payload: { at: Date; token: string; uid: string }) {
    firebase.auth().onAuthStateChanged(user => {
      if (!user || user.uid !== payload.uid) {
        const now = new Date()

        if (now.getTime() - payload.at.getTime() < 30 * 60 * 1000) {
          context.dispatch('login', payload.token)
        } else {
          // TODO - We should ask for a new token here.
          context.dispatch('logged', false)
          context.dispatch('showButterbar', { message: 'ERROR_AUTHENTICATION_LOGGED_OUT', link: 'ERROR_AUTHENTICATION_LOGGED_OUT_LINK' }, { root: true })
          window._logger(new Error(`User has been logged out after ${now.getTime() - payload.at.getTime()}`))
        }

        return
      }

      context.dispatch('logged', true)
    })
  },
  login(context, token: string) {
    if (!token) {
      context.dispatch('logged', false)
      context.dispatch('showButterbar', { message: 'ERROR_AUTHENTICATION_FAILED', link: 'ERROR_AUTHENTICATION_FAILED_LINK' }, { root: true })
      window._logger(new Error('Unable to sign user in. No token has been provided.'))
      return
    }

    firebase
      .auth()
      .signInWithCustomToken(token)
      .catch(function(error) {
        if (error) {
          context.dispatch('logged', false)
          context.dispatch('showButterbar', { message: 'ERROR_AUTHENTICATION_FAILED', link: 'ERROR_AUTHENTICATION_FAILED_LINK' }, { root: true })
          window._logger(error)
          return
        }

        context.dispatch('logged', true)
      })
  },

  logged(context, isLogged: boolean) {
    if (isLogged) {
      context.dispatch('templates/initialize', null, { root: true })
    }

    context.commit('logged', isLogged)
  }
}

const mutations: MutationTree<Types.UserProfile> = {
  set(state, user) {
    state.uid = user.uid
    state.name = user.name
    state.email = user.email
    state.domain = user.domain
    state.premium = user.premium
    state.blocked = user.blocked
    state.subscription = user.subscription
    state.notificationsUnread = user.notificationsUnread

    Vue.set(state, 'aliases', user.aliases)
    Vue.set(state, 'quota', user.quota)
  },

  logged(state, newValue: boolean) {
    state.isLogged = newValue
  }
}

export default {
  namespaced: true,
  state,
  // getters,
  actions,
  mutations
}
