import { ActionTree, MutationTree } from 'vuex'
import * as firebase from 'firebase/app'
import 'firebase/database'
import Types from '../../types'

// Initialize Firebase
const eventConfig = window._app && window._app.firebase_events[0]
const eventDB = firebase.initializeApp(eventConfig, 'event')

// State
const state: Types.CampaignEvent[] = []

const actions: ActionTree<Types.Campaign[], Types.RootState> = {
  /**
   * Watch a campaign's events
   */

  listener(context, campaign: Types.Campaign) {
    // Firebase events listener for clicks
    firebase
      .database(eventDB)
      .ref('/events/open/' + campaign.cid)
      .on('child_added', function(snapshot) {
        const eventCode = 'EMAIL_OPENED'
        context.commit('stack', { cid: campaign.cid, column: campaign.column, eid: snapshot.key, status: eventCode })
      })

    // Firebase events listener for clicks
    firebase
      .database(eventDB)
      .ref('/events/click/' + campaign.cid)
      .on('child_added', function(snapshot) {
        const eventCode = 'EMAIL_CLICKED'
        context.commit('stack', { cid: campaign.cid, column: campaign.column, eid: snapshot.key, status: eventCode })
      })
  },

  /**
   * Update a spreadsheet with new campaigns events
   * every 2.5 seconds
   */

  timer(context) {
    if (context.state.length) {
      window._backend.sheetUIUpdateSheetWithEvents(context.state)

      context.commit('clear')
    }

    window.setTimeout(() => {
      context.dispatch('timer')
    }, 2500)
  }
}

const mutations: MutationTree<Types.Campaign[]> = {
  /**
   * Stack all campaigns events
   */

  stack(state, event: Types.CampaignEvent) {
    if (!event.eid) {
      return
    }

    state.push(event)
  },

  /**
   * Clear all campaigns events
   */

  clear(state) {
    while (state.length > 0) {
      state.pop()
    }
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations
}
